import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { isNullOrUndefined } from 'util';

import { EstateService, EstateSummary } from '@runway/estate';
import { HandlListComponent, HandlParams, HandlService, HandlSummary } from '@runway/handl';
import { HomeService, RangeSummary } from '@runway/home';

import { DataSharingService } from '../../data-sharing.service';

import { HandLFilter } from '../handl-filter.model';

import { SearchHandlPipe } from '../search-handl.pipe';

import { environment } from '../../../environments/environment';

@Component({
  selector: 'app-hnl',
  templateUrl: './hnl.component.html'
})
export class HnlComponent extends HandlListComponent implements OnInit {

  estateListV2: EstateSummary[];
  suburbList: string[];
  selectedBuilder: string;
  estateCpId = environment.estateCpId;
  buildersList = [];

  bedroom1check = false;
  bedroom2check = false;
  bedroom3check = false;
  bedroom4check = false;
  bedroom5check = false;

  minPriceRange = 0;
  maxPriceRange = 1000000;
  minFrontage = 0;
  maxFrontage = 30;

  searchFilteredList: HandlSummary[]; // Filtered Array (Local)

  // Model Object to hold selected filter values
  searchHandlFilter: HandLFilter;
  pageLoaded = false;

  constructor(public handlService: HandlService, public homeService: HomeService, public estateService: EstateService,
              protected handParams: HandlParams, protected router: Router,
              private dataSharingService: DataSharingService, private searchHouseAndLand: SearchHandlPipe) {
    super(handlService, handParams, router);
    this.searchHandlFilter = new HandLFilter();

    this.searchHandlFilter.noofbedrooms = 0;
    this.searchHandlFilter.bedroomsArray = [];
    this.searchFilteredList = [];
  }

  ngOnInit() {
    this.pageLoaded = false;
    this.listHandLSummary();
  }

  onPriceChange(event) {
    this.searchHandlFilter.priceFrom = event.from;
    this.searchHandlFilter.priceTo = event.to;
    this.searchFilteredList = this.searchHouseAndLand.transform(this.dataSharingService.getHandlSummaries(), this.searchHandlFilter);
  }

  onFrontageChange(event) {
    this.searchHandlFilter.frontageFrom = event.from;
    this.searchHandlFilter.frontageTo = event.to;
    this.searchFilteredList = this.searchHouseAndLand.transform(this.dataSharingService.getHandlSummaries(), this.searchHandlFilter);
  }

  listHandLSummary() {
    return this.handlService.listPublishedHandlSummaryByEstate(this.estateCpId)
      .subscribe((handlList) => {
        this.dataSharingService.setHandlSummaries(handlList);
        this.setBuildersList(handlList);
        this.setMinAndMaxPriceValues();
        this.pageLoaded = true;
      });
  }

  setBuildersList(list) {
    list.forEach(el => {
      if (!this.buildersList.includes(el.buildername)) {
        this.buildersList.push(el.buildername);
      }
    });
  }

  searchBuilder(builder) {
    this.searchHandlFilter.builder = builder;
    this.searchFilteredList = this.searchHouseAndLand.transform(this.dataSharingService.getHandlSummaries(), this.searchHandlFilter);
  }

  onHandlClick(selectedPackage: HandlSummary) {
    const link = ['/houseandland-detail', selectedPackage.cpidstring];
    this.router.navigate(link);
  }

  searchBedrooms(bedroom: any) {
    this.searchHandlFilter.bedroomsArray = [];

    if (this.bedroom1check) {
      this.searchHandlFilter.bedroomsArray.push(1);
    }
    if (this.bedroom2check) {
      this.searchHandlFilter.bedroomsArray.push(2);
    }
    if (this.bedroom3check) {
      this.searchHandlFilter.bedroomsArray.push(3);
    }
    if (this.bedroom4check) {
      this.searchHandlFilter.bedroomsArray.push(4);
    }
    if (this.bedroom5check) {
      this.searchHandlFilter.bedroomsArray.push(5);
    }
    this.searchFilteredList = this.searchHouseAndLand.transform(this.dataSharingService.getHandlSummaries(), this.searchHandlFilter);
  }

  setMinAndMaxPriceValues() {
    let minPrice = 0;
    let maxPrice = 0;
    let minFrontage = 0;
    let maxFrontage = 0;
    if (!isNullOrUndefined(this.dataSharingService.getHandlSummaries())) {
        this.dataSharingService.getHandlSummaries().forEach(handl => {
            if ((minPrice === 0 || handl.productprice < minPrice) && !isNullOrUndefined(handl.productprice)) {
                minPrice = handl.productprice;
            }
            if (maxPrice === 0 || handl.productprice > maxPrice) {
                maxPrice = handl.productprice;
            }
            if ((minFrontage === 0 || handl.lotwidth < minFrontage) && !isNullOrUndefined(handl.lotwidth)) {
                minFrontage = handl.lotwidth;
            }
            if ((maxFrontage === 0 || handl.lotwidth > maxFrontage) && !isNullOrUndefined(handl.lotwidth)) {
                maxFrontage = handl.lotwidth;
            }
        });
    }
    if (minPrice !== 0) {
        minPrice = (Math.round(minPrice / 10000) - 1) * 10000;
    }
    maxPrice = Math.floor(Math.round(maxPrice / 10000) + 1) * 10000;
    this.minPriceRange = minPrice;
    this.maxPriceRange = maxPrice;
    this.minFrontage = minFrontage;
    this.maxFrontage = maxFrontage;
    this.searchHandlFilter.priceFrom = minPrice;
    this.searchHandlFilter.priceTo = maxPrice;
    this.searchHandlFilter.frontageFrom = minFrontage;
    this.searchHandlFilter.frontageTo = maxFrontage;
    this.searchFilteredList = this.searchHouseAndLand.transform(this.dataSharingService.getHandlSummaries(), this.searchHandlFilter);
}
}



