import { Pipe, PipeTransform } from '@angular/core';

import { LotSummary } from '@runway/land';

import { isNullOrUndefined } from 'util';

import { LandFilter } from './land-filter.model';

import { environment } from '../../environments/environment';

@Pipe({
    name: 'searchLandPipe'
})
export class SearchLandPipe implements PipeTransform {

    transform(LandArray: LotSummary[], searchFilter: LandFilter): any {
        if (isNullOrUndefined(searchFilter)) {
            return LandArray;
        }

        return LandArray.filter((lot) => {
            return (isNullOrUndefined(searchFilter.status) || (lot.currentstatusname === searchFilter.status))
                && (isNullOrUndefined(searchFilter.stage) || (lot.stageproductname === searchFilter.stage))
                && (lot.productprice >= searchFilter.priceFrom)
                && (lot.productprice <= searchFilter.priceTo)
                && (lot.productsize >= searchFilter.areaFrom)
                && (lot.productsize <= searchFilter.areaTo)
                && (lot.productwidth >= searchFilter.frontageFrom)
                && (lot.productwidth <= searchFilter.frontageTo);
        });
    }

}



