import { Component, AfterViewInit, DoCheck } from '@angular/core';
import { DomSanitizer } from '@angular/platform-browser';
import { Headers, Http, RequestOptions, Response } from '@angular/http';
import { ActivatedRoute, Params, Router } from '@angular/router';

import { LandDetailComponent, LandService } from '@runway/land';

import { LoanCalculatorParams, LoanCalculatorService } from '@runway/commonutils';

import { TrackingService } from '../tracking-service/tracking.service';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-land-detail',
  templateUrl: './land-detail-extended.component.html'
})
export class LandDetailExtendedComponent extends LandDetailComponent implements AfterViewInit {

  private options = {
    lotStyles: {
      stroke: '#373C40',
      strokeWidth: 1.5,
      fill: '#ea8d8d',
      color: '#fffff'
    }
  };

  today = Date.now();
  productId: string;
  productType = 'Land';
  stagePlanUrl;

  constructor(
    public landService: LandService,
    public activatedRoute: ActivatedRoute,
    private trackingService: TrackingService,
    public sanitizer: DomSanitizer,
    private http: Http,
    private router: Router
  ) {
    super(landService, activatedRoute);
    window.scrollTo(0, 0);

    this.activatedRoute.params.forEach((params) => {
      this.productId = params['id'];
      this.fetchLotSummary();
    });

  }

  ngAfterViewInit() {
    this.activatedRoute.fragment.subscribe(fragment => {
      if (fragment) {
        this.scrollToView('register-form');
      }
    });
  }

  fetchLotSummary() {
    this.landService.getLotSummary(this.productId).subscribe(data => {
      this.eventTracking('Lot ' + data.productname);
      this.stagePlanUrl = this.sanitizer.bypassSecurityTrustResourceUrl('https://d3myrsvzulg5g3.cloudfront.net/lot-highlight-view/'
                          + environment.r6SecurityToken + '/' + data.stagecpidstring + '/' + data.productname);
    });
  }

  eventTracking(referrer) {
    const event = 'Viewed Land Detail';
    this.trackingService.eventTracking(event, referrer, this.productId, this.productType);
  }

  scrollToView(id) {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({behavior: 'smooth'});
    } else {
      this.router.navigate(['contact']);
    }
  }

}



