import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { AssetModule } from '@runway/asset';
import { CommonUtilsModule, LoanCalculatorParams } from '@runway/commonutils';
import { EstateModule } from '@runway/estate';
import { HandlModule, HandlParams } from '@runway/handl';
import { LandModule, LandParams } from '@runway/land';
import { MapsModule } from '@runway/maps';
import { MasterplanModule, MasterplanParams } from '@runway/r4masterplan';
import { R6ApiModule, R6ApiParams } from '@runway/r6api';

import { EnquiryService } from '@runway/enquiry';
import { HomeService } from '@runway/home';

import { CmsApiService } from './cms-service/cms.service';
import { ContactDetailsService } from './contact/contact-details-service';
import { DataSharingService } from './data-sharing.service';
import { EnquiryExtendedService } from './enquiry-form/enquiry.service';
import { EnvironmentService } from './environment.service';
import { ThemeControlService } from './theme.service';
import { TrackingService } from './tracking-service/tracking.service';

import { AboutModule } from './about/about.module';
import { ContactModule } from './contact/contact.module';
import { HeaderFooterModule } from './header-footer/header-footer.module';
import { HomePageModule } from './home-page/home-page.module';
import { HnlModule } from './hnl/hnl.module';
import { LandListModule } from './landlist/landlist.module';
import { LocationModule } from './location/location.module';
import { LandForSaleModule } from './land-for-sale/land-for-sale.module';
import { MapDirectionModule } from './map-direction/map-direction.module';
import { NewsModule } from './news/news.module';

import { AppComponent } from './app.component';

import { appRouting, appRoutingProviders } from './app.routing';

@NgModule({
  declarations: [
    AppComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    HttpModule,
    RouterModule,
    AssetModule,
    CommonUtilsModule,
    EstateModule,
    HandlModule,
    HnlModule,
    LandModule,
    MapDirectionModule,
    MapsModule,
    MasterplanModule,
    NewsModule,
    R6ApiModule,
    AboutModule,
    ContactModule,
    HeaderFooterModule,
    HomePageModule,
    LandListModule,
    LandForSaleModule,
    LocationModule,
    appRouting
  ],
  providers: [
    appRoutingProviders,
    CmsApiService,
    ContactDetailsService,
    DataSharingService,
    HomeService,
    ThemeControlService,
    TrackingService,
    { provide: EnquiryService, useClass: EnquiryExtendedService },
    { provide: HandlParams, useClass: EnvironmentService },
    { provide: LandParams, useClass: EnvironmentService },
    { provide: MasterplanParams, useClass: EnvironmentService },
    { provide: LoanCalculatorParams, useClass: LoanCalculatorParams },
    { provide: R6ApiParams, useClass: EnvironmentService },
  ],
  bootstrap: [AppComponent]
})
export class AppModule {
}



