import { ActivatedRoute } from '@angular/router';
import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';

import { CmsApiService } from '../cms-service/cms.service';
import { NewsItems } from './news.model';

import { environment } from '../../environments/environment';

@Component({
    selector: 'app-news',
    templateUrl: './news-detail.component.html'
})
export class NewsDetailComponent implements OnInit {

    post: NewsItems = new NewsItems;
    newsId: string;

    constructor(private cmsApiService: CmsApiService, private route: ActivatedRoute) {
    }

    ngOnInit() {
        this.route.params.forEach((params) => {
            this.newsId = params['id'];
            this.fetchData();
        });
    }

    fetchData() {
        return this.cmsApiService.getAllContent()
            .subscribe(data => {
                this.filterNews(data);
            });
    }

    filterNews(data) {
        const items = data.find(
            item => (item.id === this.newsId)
        );
        this.createNews(items);
    }

    createNews(item) {
        this.post = {
            id: item.id,
            parentId: item.parentId,
            createdDate: item.createDate,
            isPublised: item.isPublised,
            title: item.properties.title,
            summary: item.properties.summary,
            body: item.properties.body,
            featuredImage: environment.cmsMediaUrl + item.properties.featured_image
        };
    }
}



