import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';

import { CommonUtilsModule } from '@runway/commonutils';
import { HandlModule, HandlParams, HandlService } from '@runway/handl';
import { LandModule, LandParams, LandService } from '@runway/land';
import { MasterplanModule, MasterplanParams } from '@runway/r4masterplan';
import { R6ApiModule, R6ApiParams } from '@runway/r6api';

import { IonRangeSliderModule } from 'ng2-ion-range-slider';
import { ContactModule } from '../contact/contact.module';
import { EnquiryFormModule } from '../enquiry-form/enquiry-form.module';
import { SimilarPckgModule } from '../hnl/hnl-master-details/similar-package/similar-pckg.module';

import { EnvironmentService } from '../environment.service';

import { LandlistExtendedComponent } from './landlist.component';
import { LandDetailExtendedComponent } from './land-detail-extended.component';

import { SearchLandPipe } from './search-land.pipe';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    CommonUtilsModule,
    HandlModule,
    LandModule,
    MasterplanModule,
    IonRangeSliderModule,
    R6ApiModule,
    ContactModule,
    EnquiryFormModule,
    SimilarPckgModule
  ],
  declarations: [SearchLandPipe, LandlistExtendedComponent, LandDetailExtendedComponent],
  exports: [LandlistExtendedComponent, LandDetailExtendedComponent],
  providers: [
    SearchLandPipe,
    LandService,
    HandlService,
    { provide: HandlParams, useClass: EnvironmentService },
    { provide: LandParams, useClass: EnvironmentService },
    { provide: MasterplanParams, useClass: EnvironmentService },
    { provide: R6ApiParams, useClass: EnvironmentService },
  ],
})
export class LandListModule { }



