import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import 'rxjs/Rx';
import { Observable } from 'rxjs/Observable';

import { JavaScriptUtilsService } from '@runway/commonutils';

import { CmsApiService } from './cms-service/cms.service';
import { ThemeControlService } from './theme.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html'
})
export class AppComponent implements OnInit {

  constructor(private router: Router, private cmsApiService: CmsApiService, private themeControlService: ThemeControlService) {
    this.fetchData();
  }

  ngOnInit() {
  }

  fetchData() {
    if (localStorage.getItem('theme')) {
      const data = localStorage.getItem('theme');
      const theme = JSON.parse(data);
      this.themeControlService.createTheme(theme);
    }

    this.cmsApiService.getAllContent()
      .subscribe(data => {
        this.themeControlService.filterItems(data);
    });
  }
}



