import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';

import { DataSharingService } from '../../../data-sharing.service';
import { GalleryService } from '@runway/asset';

import { HandlService, HandlSummary, HandlDetailComponent } from '@runway/handl';
import { HomeSummary, HomeService } from '@runway/home';
import { HouseDetailsComponent } from './../hnl-details/housedetails.component';

@Component({
  selector: 'app-similar-package',
  templateUrl: './similar-pckg.component.html'
})
export class SimilarPckgComponent extends HandlDetailComponent implements OnChanges {

  @Input() selectedHandlSummary: HandlSummary;
  @Input() selectedHomeSummary: HomeSummary;

  similarHandlSummaries: HandlSummary[];
  pageLoaded = false;

  constructor(protected dataSharingService: DataSharingService, protected handlService: HandlService,
    protected router: Router, protected galleryService: GalleryService, protected activatedRoute: ActivatedRoute) {
    super(handlService, galleryService, activatedRoute);
  }


  ngOnChanges(changes: SimpleChanges) {
    if (changes['selectedHandlSummary']) {
      this.fetchRelatedPackagesForLot();
    }
    if (changes['selectedHomeSummary']) {
      if (this.dataSharingService.getHandlSummaries().length === 0) {
        this.handlService.listHandlSummaryByEstate(null)
          .subscribe((handlList) => {
            this.dataSharingService.setHandlSummaries(handlList);
            this.fetchRelatedPackagesForHome();
          });
      } else {
        this.fetchRelatedPackagesForHome();
      }
    }
  }

  onHandlClick(selectedPackage: HandlSummary) {
    const link = ['/houseandland-detail', selectedPackage.cpidstring];
    this.router.navigate(link);
  }

  private fetchRelatedPackagesForLot() {
    this.similarHandlSummaries = this.dataSharingService.getHandlSummaries()
      .filter(handlSummary => handlSummary.cpidstring !== this.selectedHandlSummary.cpidstring
        && handlSummary.lotcpidstring === this.selectedHandlSummary.lotcpidstring);
        this.pageLoaded = true;
  }

  private fetchRelatedPackagesForHome() {
    this.similarHandlSummaries = this.dataSharingService.getHandlSummaries()
      .filter(handlSummary => handlSummary.cpidstring !== this.selectedHomeSummary.cpidstring
        && handlSummary.designproductname === this.selectedHomeSummary.designproductname);
  }
}



