import { Component } from '@angular/core';

import { ContactPageDetails } from '../contact/contact.model';
import { ContactDetailsService } from '../contact/contact-details-service';

@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html'
})
export class FooterComponent {

  contactPageDetails: ContactPageDetails = new ContactPageDetails;

  constructor(private contactDetailsService: ContactDetailsService) {
      this.fetchData();
  }

  fetchData() {
    if (localStorage.getItem('contact')) {
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
    }

    this.contactDetailsService.fetchData()
    .subscribe(data => {
      this.contactDetailsService.filterItems(data);
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
    });
  }

}



