import { ModuleWithProviders } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { AboutComponent } from './about/about.component';
import { ContactComponent } from './contact/contact.component';
import { HomePageComponent } from './home-page/home-page.component';
import { HnlComponent } from './hnl/hnl-list/hnl.component';
import { HouseDetailsComponent } from './hnl/hnl-master-details/hnl-details/housedetails.component';
import { LandlistExtendedComponent } from './landlist/landlist.component';
import { LandDetailExtendedComponent } from './landlist/land-detail-extended.component';
import { LandForSaleComponent } from './land-for-sale/land-for-sale.component';
import { LocationComponent } from './location/location.component';
import { NewsComponent } from './news/news.component';
import { NewsDetailComponent } from './news/news-detail.component';

const appRoutes: Routes = [
  { path: '', redirectTo: '/homepage', pathMatch: 'full' },
  { path: 'homepage', component: HomePageComponent },
  { path: 'about', component: AboutComponent },
  { path: 'houseandland-list', component: HnlComponent },
  { path: 'houseandland-detail/:id', component: HouseDetailsComponent },
  { path: 'land-list', component: LandlistExtendedComponent },
  { path: 'land-detail/:id', component: LandDetailExtendedComponent },
  { path: 'land-for-sale', component: LandForSaleComponent },
  { path: 'location', component: LocationComponent },
  { path: 'news/:id', component: NewsDetailComponent },
  { path: 'news', component: NewsComponent },
  { path: 'contact', component: ContactComponent }
];

export const appRoutingProviders: any[] = [];

export const appRouting: ModuleWithProviders = RouterModule.forRoot(appRoutes);



