import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Subscription } from 'rxjs/Subscription';

import { isNullOrUndefined } from 'util';

import { HandlDetailComponent, HandlService, HandlSummary, HandlDetail } from '@runway/handl';
import { LandListComponent, LotSummary, LotDetail, LandService, EstateSummary, LandParams } from '@runway/land';
import { PaginationHelper, RangeFilter } from '@runway/commonutils';
import { R6ApiParams } from '@runway/r6api';

import { LandFilter } from './land-filter.model';

import { SearchLandPipe } from './search-land.pipe';

import { environment } from '../../environments/environment';

export const areaRangeList: RangeFilter[] = [
    new RangeFilter(0, 100, '0-100'),
    new RangeFilter(101, 200, '101-300000'),
    new RangeFilter(201, 300, '201-500000'),
    new RangeFilter(301, 400, '301-1000000'),
    new RangeFilter(401, 500, '401-1000000'),
    new RangeFilter(501, -1, '501+'),
];

@Component({
    selector: 'app-landlist',
    templateUrl: './landlist.component.html'
})
export class LandlistExtendedComponent extends LandListComponent implements OnInit {

    selectedAreaRange: RangeFilter;
    selectedStage: string;
    selectedProject: string;
    selectedStatus: string;
    estateSummaryList: EstateSummary[];
    estateNameList: string[];
    estateCpId = environment.estateCpId;

    handlSummaryList: HandlSummary[];
    lotList: LotSummary[];
    searchFilteredList: LotSummary[];
    stageList = [];
    pageLoaded = false;

    minPriceRange = 10000;
    maxPriceRange = 100000;
    minFrontage = 0;
    maxFrontage = 25;
    minArea = 0;
    maxArea = 1000;

    // Model Object to hold selected filter values
    searchLandFilter: LandFilter;

    constructor(public landService: LandService, public router: Router, private searchLand: SearchLandPipe, public landParams: LandParams, public handlService: HandlService) {
        super(router, landService, landParams);

        this.searchLandFilter = new LandFilter();

        this.searchLandFilter.priceFrom = 0;
        this.searchLandFilter.priceTo = 800000;
        this.searchLandFilter.areaFrom = 0;
        this.searchLandFilter.areaTo = 1000;
        this.searchLandFilter.frontageFrom = 0;
        this.searchLandFilter.frontageTo = 25;
        this.searchLandFilter.status = 'Available';
        this.lotList = [];
        this.searchFilteredList = this.lotList;
    }

    ngOnInit() {
        // /* Override default behaviour of LandList by setting lotStatus = Available*/
        this.lotCriteria.status = 'Available';
        this.lotSummaryList = [];
        this.estateSummaryList = [];
        this.handlSummaryList = [];
        this.estateNameList = [];
        this.listHandLSummary();
    }

  listHandLSummary() {
    return this.handlService.listPublishedHandlSummaryByEstate(this.estateCpId)
      .subscribe((handlList) => {
        this.handlSummaryList = handlList;
        this.listLotSummary();
      });
  }

  checkPackage() {
    this.handlSummaryList.forEach((handlSummary: HandlSummary) => {
        if (this.lotList !== undefined) {
            this.lotList.forEach(lot => {
                if (handlSummary.lotproductname === lot.productname) {
                    lot.packaged = true;
                  }
            });
        }
      });
  }

    listLotSummary(): Subscription {
        return this.landService.listLotSummaryByEstate(this.estateCpId)
            .subscribe((lotList) => {
                this.lotList = lotList;
                this.setStageSummary();
                this.checkPackage();
                this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
                this.setMinAndMaxPriceValues();
                this.pageLoaded = true;
            });
    }

    setStageSummary() {
        this.lotList.forEach(element => {
            if (!this.stageList.includes(element.stageproductname)) {
                this.stageList.push(element.stageproductname);
            }
        });
    }

    setLotListSummaryResponse(lotList: LotSummary[]) {
        this.lotSummaryList = lotList;
        this.lotNumberList = [];
        for (const lotSummary of this.lotSummaryList) {
            this.lotNumberList.push(lotSummary.productname);
        }
    }

    onLotClick(selectedLot: LotSummary) {
        const link = ['/land-detail', selectedLot.cpidstring];
        this.router.navigate(link);
    }

    goToLotEnquiry(selectedLot: LotSummary) {
        const link = ['/land-detail', selectedLot.cpidstring];
        this.router.navigate(link, {fragment: 'register-form'});
    }

    onPriceChange(event) {
        this.searchLandFilter.priceFrom = event.from;
        this.searchLandFilter.priceTo = event.to;
        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    onAreaChange(event) {
        this.searchLandFilter.areaFrom = event.from;
        this.searchLandFilter.areaTo = event.to;

        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    onFrontageChange(event) {
        this.searchLandFilter.frontageFrom = event.from;
        this.searchLandFilter.frontageTo = event.to;
        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    onStageChange() {
        if (this.selectedStage !== 'undefined') {
            this.searchLandFilter.stage = this.selectedStage;
        } else {
            this.searchLandFilter.stage = null;
        }
        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    searchStatus(status: string) {
        this.searchLandFilter.status = status;
        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    onLotProjectChange(selectedProjectName: string) {
        super.resetPagination();
        this.lotCriteria.estatename = selectedProjectName;

        this.searchLandFilter.project = this.lotCriteria.estatename;

        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

    getAreaRangeList(): RangeFilter[] {
        return areaRangeList;
    }

    contactPage(productId) {
        this.router.navigate(['contact']);
    }

    setMinAndMaxPriceValues() {
        let minPrice = 0;
        let maxPrice = 0;
        let minFrontage = 0;
        let maxFrontage = 0;
        let minArea = 0;
        let maxArea = 0;
        if (!isNullOrUndefined(this.searchFilteredList)) {
            this.searchFilteredList.forEach(handl => {
                if ((minPrice === 0 || handl.productprice < minPrice) && handl.productprice !== null) {
                    minPrice = handl.productprice;
                }
                if (maxPrice === 0 || handl.productprice > maxPrice) {
                    maxPrice = handl.productprice;
                }
                if ((minFrontage === 0 || handl.productwidth < minFrontage) && handl.productwidth !== null) {
                    minFrontage = handl.productwidth;
                }
                if (maxFrontage === 0 || handl.productwidth > maxFrontage) {
                    maxFrontage = handl.productwidth;
                }
                if ((minArea === 0 || handl.productsize < minArea) && handl.productsize !== null) {
                    minArea = handl.productsize;
                }
                if (maxArea === 0 || handl.productsize > maxArea) {
                    maxArea = handl.productsize;
                }
            });
        }
        if (minPrice !== 0) {
            minPrice = (Math.round(minPrice / 10000) - 1) * 10000;
        }
        maxPrice = Math.floor(Math.round(maxPrice / 10000) + 1) * 10000;
        this.minPriceRange = minPrice;
        this.maxPriceRange = maxPrice;
        this.minFrontage = minFrontage;
        this.maxFrontage = maxFrontage;
        this.minArea = minArea;
        this.maxArea = maxArea;
        this.searchLandFilter.priceFrom = minPrice;
        this.searchLandFilter.priceTo = maxPrice;
        this.searchLandFilter.frontageFrom = minFrontage;
        this.searchLandFilter.frontageTo = maxFrontage;
        this.searchLandFilter.areaFrom = minArea;
        this.searchLandFilter.areaTo = maxArea;
        this.searchFilteredList = this.searchLand.transform(this.lotList, this.searchLandFilter);
    }

}



