import { Component, OnInit } from '@angular/core';
import 'rxjs/Rx';

import { CmsApiService } from '../cms-service/cms.service';
import { NewsItems } from './news.model';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-news',
  templateUrl: './news.component.html'
})
export class NewsComponent implements OnInit {

  news: any = [];

  constructor(private cmsApiService: CmsApiService) {
  }

  ngOnInit() {
    this.fetchData();
  }

  fetchData() {
    return this.cmsApiService.getAllContent()
      .subscribe(data => {
        this.filterNews(data);
      });
  }

  filterNews(data) {
    const items = data.filter(
      item => (item.parentId === 'news')
    );
    this.createNews(items);
  }

  createNews(items) {
    items.forEach(item => {
      const post: NewsItems = {
        id: item.id,
        parentId: item.parentId,
        createdDate: item.createDate,
        isPublised: item.isPublised,
        title: item.properties.title,
        summary: item.properties.summary,
        body: item.properties.body,
        featuredImage: environment.cmsMediaUrl + item.properties.featured_image
      };
      this.news.push(post);
    });
  }
}



