import { Component, OnInit } from '@angular/core';
import { Observable } from 'rxjs/Observable';

import { AboutPageDetails } from './about.model';
import { AtaGlanceItems } from './ataglance.model';
import { CmsApiService } from '../cms-service/cms.service';
import { environment } from '../../environments/environment';

@Component({
  selector: 'app-about',
  templateUrl: './about.component.html'
})
export class AboutComponent implements OnInit {

  aboutPageDetail: AboutPageDetails = new AboutPageDetails;
  atGlanceItems = [];

  constructor(private cmsApiService: CmsApiService) {
  }

  ngOnInit() {
    this.fetchData();
  }

  fetchData() {
    return this.cmsApiService.getAllContent()
      .subscribe(data => {
        this.filterItems(data);
      });
  }

  filterItems(data) {
    const pageItem = data.find(
      item => (item.id === 'about')
    );
    const glanceItems = data.filter(
      item => (item.parentId === 'about')
    );
    this.createPageDetail(pageItem);
    this.createPageItems(glanceItems);
  }

  createPageDetail(item) {
    this.aboutPageDetail = {
      title: item.properties.headline,
      body: item.properties.body,
      image: environment.cmsMediaUrl + item.properties.featured_image,
      secondary_title: item.properties.secondary_headline,
      secondary_body: item.properties.secondary_body,
      secondary_image: item.properties.secondary_image
    };
  }

  createPageItems(items) {
    items.forEach(item => {
      const atGlanceItems: AtaGlanceItems = {
        icon: item.properties.icon,
        time: item.properties.time,
        subtitle: item.properties.subtitle
      };
      this.atGlanceItems.push(atGlanceItems);
    });
  }
}



