import { Component } from '@angular/core';

import { ContactPageDetails } from './contact.model';
import { ContactDetailsService } from './contact-details-service';
import { GoogleMapsLoader } from '../map-direction/map-loader.service';

declare var google: any;

@Component({
  selector: 'app-contact',
  templateUrl: './contact.component.html'
})
export class ContactComponent {

  contactPageDetails: ContactPageDetails = new ContactPageDetails;
  map: any;
  mapDirectionUrl: string;

  constructor(private contactDetailsService: ContactDetailsService) {
    GoogleMapsLoader.load()
      .then(res => {
        this.fetchData();
      });
  }

  fetchData() {
    if (localStorage.getItem('contact')) {
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
    }

    this.contactDetailsService.fetchData()
    .subscribe(data => {
      this.contactDetailsService.filterItems(data);
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
      this.loadMap();
    });
  }

  loadMap() {
    // tslint:disable-next-line:max-line-length
    this.mapDirectionUrl = 'http://maps.google.com/?daddr=' + this.contactPageDetails.latitude + ',' + this.contactPageDetails.longitude + '&directionsmode=driving';
    const center = new google.maps.LatLng(this.contactPageDetails.latitude, this.contactPageDetails.longitude);
    const mapProp = {
      center: center,
      zoom: 15,
      mapTypeId: google.maps.MapTypeId.ROADMAP,
      disableDefaultUI: true,
      scaleControl: true,
      zoomControl: true,
    };
    this.map = new google.maps.Map(document.getElementById('googleMap'), mapProp);
    const marker = new google.maps.Marker({
      position: center,
      map: this.map,
      icon: {
        url: 'https://s3-ap-southeast-2.amazonaws.com/sog-microsites-assets/fairmile/fairmile_map_icon%402x.png',
        scaledSize: new google.maps.Size(80, 60)
      }
    });
  }

}



