import { Pipe, PipeTransform } from '@angular/core';

import { HandlSummary } from '@runway/handl';

import { isNullOrUndefined } from 'util';

import { HandLFilter } from './handl-filter.model';

@Pipe({
    name: 'searchHandlPipe'
})
export class SearchHandlPipe implements PipeTransform {

    transform(houseandlandArray: HandlSummary[], searchFilter: HandLFilter): any {
        if (isNullOrUndefined(searchFilter)) {
            return houseandlandArray;
        }

        return houseandlandArray.filter((handl) => {
            return ((searchFilter.bedroomsArray.length === 0) || (searchFilter.bedroomsArray.includes(handl.noofbedrooms)))
                && (handl.productprice >= searchFilter.priceFrom)
                && (handl.productprice <= searchFilter.priceTo)
                && (handl.lotwidth >= searchFilter.frontageFrom)
                && (handl.lotwidth <= searchFilter.frontageTo)
                && ((isNullOrUndefined(searchFilter.builder)) || (handl.buildername === searchFilter.builder) || (searchFilter.builder === 'undefined'));
        });
    }

}



