import { Component, OnInit, HostListener } from '@angular/core';
import {Router, NavigationEnd} from '@angular/router';

import { TrackingService } from '../tracking-service/tracking.service';

declare const ClientJS: any;
declare const Fingerprint2: any;

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html'
})

export class HeaderComponent implements OnInit {

  isSidebarOpen = false;
  currentPage = 'homepage';
  isPage = false;
  defaultMessage = 'Visit Page';
  trackingDetails = [];

  constructor(private router: Router, private trackingService: TrackingService) {
  }

  @HostListener('window:beforeunload', ['$event'])
    beforeunloadHandler(event) {
        this.checkWindowUnload();
    }

    @HostListener('document:click', ['$event', '$event.target'])
      onClick(event: MouseEvent, targetElement: HTMLElement): void {
        this.isPage = true;
    }

  ngOnInit() {
    this.checkSession();
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this.currentPage = (this.router.url).split('/')[1];
      }
    });
  }

  checkWindowUnload() {
    if (!this.isPage) {
      this.trackingService.stopTracking();
    }
  }

  checkSession() {
    if (sessionStorage.getItem('trackerid') === null) {
      this.trackingService.initiateTracking();
    } else {
      this.trackingService.eventTracking();
    }
  }

  toggleClick() {
    if (this.isSidebarOpen === true) {
      document.getElementById('sidemenu').classList.remove('active');
      this.isSidebarOpen = false;
      return;
    }
    document.getElementById('sidemenu').classList.add('active');
    this.isSidebarOpen = true;
  }

  scrollToView(id) {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({behavior: 'smooth'});
    } else {
      this.router.navigate(['contact']);
    }
  }

}



