import { CommonModule } from '@angular/common';
import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { FormsModule } from '@angular/forms';
import { NgModule } from '@angular/core';

import { CommonUtilsModule } from '@runway/commonutils';
import { HandlModule, HandlParams, HandlService } from '@runway/handl';
import { R6ApiModule, R6ApiParams } from '@runway/r6api';

import { IonRangeSliderModule } from 'ng2-ion-range-slider';
import { NgxGalleryModule } from 'ngx-gallery';

import { EnvironmentService } from '../environment.service';

import { HnlComponent } from './hnl-list/hnl.component';
import { HouseDetailsComponent } from './hnl-master-details/hnl-details/housedetails.component';

import { ContactModule } from '../contact/contact.module';
import { EnquiryFormModule } from '../enquiry-form/enquiry-form.module';
import { SimilarPckgModule } from './hnl-master-details/similar-package/similar-pckg.module';

import { SearchHandlPipe } from './search-handl.pipe';

import { EnquiryFormComponent } from './../enquiry-form/enquiry-form.component';

@NgModule({
  imports: [
    CommonModule,
    BrowserModule,
    FormsModule,
    CommonUtilsModule,
    BrowserAnimationsModule,
    ContactModule,
    EnquiryFormModule,
    HandlModule,
    IonRangeSliderModule,
    NgxGalleryModule,
    SimilarPckgModule,
    R6ApiModule,
  ],
  declarations: [SearchHandlPipe, HnlComponent, HouseDetailsComponent],
  exports: [HnlComponent, HouseDetailsComponent],
  entryComponents: [EnquiryFormComponent],
  providers: [
    SearchHandlPipe,
    HandlService,
    { provide: HandlParams, useClass: EnvironmentService },
    { provide: R6ApiParams, useClass: EnvironmentService },
  ],
})
export class HnlModule { }



