import { Injectable } from '@angular/core';

import { environment } from '../environments/environment';

@Injectable()
export class EnvironmentService {

  public getR6ApiUrl(): string {
    return environment.r6apiServiceUrl;
  }

  public getR6SecurityToken(): string {
    return environment.r6SecurityToken;
  }

  public getR6AuthApiKey(): string {
    return environment.r6AuthApiKey;
  }

  public getMasterplanUrl(): any {
    return environment.masterplanUrl;
  }

  public getPollingIntervalForApi(): number {
    return environment.pollingIntervalForApi;
  }

  getPageSizeForHomeList(): number {
    return environment.pageSizeForHomeList;
  }

  getPageSizeForHandlList(): number {
    return environment.pageSizeForHandlList;
  }

  getPageSizeForLandList(): number {
    return environment.pageSizeForLandList;
  }
}



