import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';

import swal from 'sweetalert2';

import { EnquiryComponent, EnquiryService, FormValidationService } from '@runway/enquiry';

import { TrackingService } from '../tracking-service/tracking.service';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-enquiry-form',
  templateUrl: './enquiry-form.component.html'
})
export class EnquiryFormComponent extends EnquiryComponent implements OnInit {
  disabledButton = false;

  constructor(private fbBuilderExtended: FormBuilder, private enquiryServiceExtended: EnquiryService,
    private routerExtended: Router, private trackingService: TrackingService) {
    super(fbBuilderExtended, enquiryServiceExtended, routerExtended);
  }

  ngOnInit() {
    this.populateForm();
  }

  populateForm() {
    this.enquiryForm = this.fbBuilderExtended.group({
      FirstName: ['', Validators.required],
      LastName: ['', Validators.required],
      Email: ['', Validators.required],
      Phone: ['', Validators.required],
      Postcode: [''],
      Comment: ['']
    });
    this.initializeProfileQuestions();
  }

  onSubmit() {
    const reference = this.enquiryForm.value.FirstName + ' ' + this.enquiryForm.value.LastName;
    this.submitTrackingEvent(reference);
    console.log(this.enquiryForm);
    if (this.enquiryForm.valid) {
      this.disabledButton = true;
      super.onSubmit(environment.globalFormUrl, null, this.message);
    }
  }

  submitTrackingEvent(reference) {
    const event = 'Submitted Contact Form';
    this.trackingService.eventTracking(event, reference, null, null);
  }

  message(isSuccess) {
    console.log(this.disabledButton.valueOf);
    if (isSuccess === true) {
      swal('Thank You', 'Your message has been sent', 'success');
    } else {
      swal({
        type: 'error',
        title: 'Something went wrong',
        showConfirmButton: false
      }).catch(swal.noop);
    }
  }

}



