import { Injectable } from '@angular/core';

import { HandlSummary } from '@runway/handl';

@Injectable()
export class DataSharingService {

  private handlSummaries: HandlSummary[] = [];

  public getHandlSummaries(): HandlSummary[] {
    return this.handlSummaries;
  }

  public setHandlSummaries(handlSummaries: HandlSummary[]) {
    this.handlSummaries = handlSummaries;
  }
}



