import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { AgmCoreModule } from '@agm/core';
import { maps } from '@google/maps';

import { CommonUtilsModule } from '@runway/commonutils';
import { R6ApiModule, R6ApiParams } from '@runway/r6api';

import { EnvironmentService } from '../environment.service';

import { MapDirectionComponent } from './map-direction.component';

@NgModule({
    imports: [
        CommonModule,
        CommonUtilsModule,
        R6ApiModule,
        AgmCoreModule.forRoot({
            apiKey: 'AIzaSyA-tvF78Fm3NiqRIpm6JCXAiaXn251Y-Gw'
        })
    ],
    declarations:[MapDirectionComponent],
    exports: [MapDirectionComponent],
    providers: [
        {provide: R6ApiModule, useClass: EnvironmentService}
    ]
})
export class MapDirectionModule{
}


