import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { CommonUtilsModule } from '@runway/commonutils';
import { LandModule, LandParams } from '@runway/land';
import { R6ApiModule, R6ApiParams } from '@runway/r6api';

import { EnvironmentService } from '../environment.service';

import { HomePageComponent } from './home-page.component';

import { ContactModule } from '../contact/contact.module';
import { EnquiryFormModule } from '../enquiry-form/enquiry-form.module';
import { HeaderFooterModule } from '../header-footer/header-footer.module';
import { HnlModule } from '../hnl/hnl.module';

@NgModule({
  imports: [
    CommonModule,
    CommonUtilsModule,
    ContactModule,
    LandModule,
    R6ApiModule,
    EnquiryFormModule,
    HnlModule,
    HeaderFooterModule
  ],
  declarations: [HomePageComponent],
  exports: [HomePageComponent],
  providers: [
    { provide: LandParams, useClass: EnvironmentService },
    { provide: R6ApiParams, useClass: EnvironmentService },
  ],
})
export class HomePageModule { }



