import { Component, OnInit } from '@angular/core';

import { CmsApiService } from '../cms-service/cms.service';
import { LocationItem } from './location.model';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-location',
  templateUrl: './location.component.html'
})
export class LocationComponent implements OnInit {

  locationPageDetail: LocationItem = new LocationItem;

  constructor(private cmsApiService: CmsApiService) {
  }

  ngOnInit() {
    this.fetchData();
  }

  fetchData() {
    return this.cmsApiService.getAllContent()
      .subscribe(data => {
        this.filterItems(data);
      });
  }

  filterItems(data) {
    const pageItem = data.find(
      item => (item.id === 'location')
    );
    this.createLocationDetail(pageItem);
  }

  createLocationDetail(item) {
    this.locationPageDetail = {
      title: item.properties.title,
      body: item.properties.body,
      image: environment.cmsMediaUrl + item.properties.image
    };
  }

}



