import { Component, OnInit } from '@angular/core';
import { NavigationEnd, Router} from '@angular/router';

import { HomePageDetails } from './home.model';
import { HomeItems } from './home-items.model';
import { CmsApiService } from '../cms-service/cms.service';

import { environment } from '../../environments/environment';

@Component({
  selector: 'app-home-page',
  templateUrl: './home-page.component.html'
})
export class HomePageComponent implements OnInit {

  homePageDetails: HomePageDetails = new HomePageDetails;
  homeItems = [];

  constructor(private cmsApiService: CmsApiService, private router: Router) {
  }

  ngOnInit() {
    this.fetchData();
  }

  fetchData() {
    return this.cmsApiService.getAllContent()
      .subscribe(data => {
        this.filterItems(data);
      });
  }

  filterItems(data) {
    const pageItem = data.find(
      item => (item.id === 'home')
    );
    const homeItems = data.filter(
      item => (item.parentId === 'home')
    );
    this.createPageDetail(pageItem);
    this.createPageItems(homeItems);
  }

  createPageDetail(item) {
    this.homePageDetails = {
      title: item.properties.headline,
      body: item.properties.body,
      image: environment.cmsMediaUrl + item.properties.featured_image
    };
  }

  createPageItems(items) {
    items.forEach(item => {
      const homePageItem: HomeItems = {
        title: item.properties.headline,
        subtitle: item.properties.subtitle,
        image: environment.cmsMediaUrl + item.properties.image
      };
      this.homeItems.push(homePageItem);
    });
  }

  scrollToView(id) {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({ behavior: 'smooth' });
    } else {
      this.router.navigate(['contact']);
    }
  }

}



