import { Injectable } from '@angular/core';
import { Headers, Http, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import 'rxjs/add/operator/catch';

import { environment } from '../../environments/environment';

@Injectable()
export class CmsApiService {

    public data;

    constructor(private http: Http) {
    }

    public getAllContent() {
        if (!this.data) {
            const url = environment.cmsApiUrl + '?token=' + environment.cmsToken;
            const headers: Headers = this.buildRequestHeader();
            this.data = this.http.get(url, {headers: headers})
                .map(response => response.json())
                .publishReplay(1)
                .refCount();
        }
        return this.data;
    }

    private buildRequestHeader() {
        const headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'Cache-control': 'no-cache, private, no-store, must-revalidate, max-age=0',
            'Expires': '0',
            'Pragma': 'no-cache'
        });
        return headers;
    }

    private handleErrorObservable(error: Response | any) {
        console.error(error);
        return Observable.throw(error);
    }
}



