import { Injectable } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Headers, Http, RequestOptions, Response } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/Rx';
import 'rxjs/add/operator/catch';

import { EventItems } from './event.model';
import { TrackingItems } from './tracking.model';

import { environment } from '../../environments/environment';

declare const imprint: any;
declare const Fingerprint2: any;
declare const Promise: any;

@Injectable()
export class TrackingService {

    initTrackingUrl = environment.trackerApi + 'initiatetrackerlog';
    eventTrackingUrl = environment.trackerApi + 'publisheventlogtracker';
    finishTrackingUrl = environment.trackerApi + 'finishtrackerlog';

    constructor(private http: Http, private router: Router) {
    }

    initiateTracking() {
        const trackingItems = new TrackingItems;
        return Promise.all([
            this.getIp().then(data => trackingItems.trackerip = data),
            this.getFingerprint().then(data => trackingItems.fingerprint = data),
            trackingItems.capsuleid = this.getCapsuleId(),
            trackingItems.referrer = this.getReferrer(),
            trackingItems.trackersource = this.getSource(),
            trackingItems.trackeragent = this.getUserAgent(),
            trackingItems.trackermessage = this.setTrackerMessage('Initated tracker'),
        ]).then(response => {
            this.postTracking(this.initTrackingUrl, trackingItems).subscribe(res => {
                if (res.trackerid) {
                    sessionStorage.setItem('trackerid', res.trackerid);
                }
            });
        }).catch(err => {
            throw err;
        });
    }

    eventTracking(event?, reference?, productId?, productType?) {
        const eventItems = new EventItems;
        return Promise.all([
            eventItems.trackerid = this.getTrackerId(),
            eventItems.productreference = this.getReference(reference),
            eventItems.capsuleid = this.getCapsuleId(),
            eventItems.productid = this.getProductId(productId),
            eventItems.producttype = this.getProductType(productType),
            eventItems.eventname = this.setTrackerMessage(event),
        ]).then(response => {
            this.postTracking(this.eventTrackingUrl, eventItems).subscribe(res => {
                return res;
            });
        });
    }

    stopTracking() {
        const trackerid = this.getTrackerId();
        this.http.get(this.finishTrackingUrl + '?trackerId=' + trackerid).map(res => {
            return res;
        }).subscribe();
        sessionStorage.removeItem('trackerid');
    }

    private getCapsuleId() {
        return environment.capsuleId;
    }

    private getFingerprint() {
        return new Promise(resolve => {
            new Fingerprint2().get(function (result) {
                resolve(result);
            });
        }).then(response => {
            return response;
        });
    }

    private getCurrentTime() {
        const now = new Date();
        return new Date(now).toISOString();
    }

    private getReferrer() {
        return document.referrer;
    }

    private getIp() {
        const url = 'https://ipapi.co/json/';
        return fetch(url)
            .then(response => {
                return response.json();
            })
            .then(data => {
                return data.ip;
            })
            .catch(err => {
                throw err;
            });
    }

    private getProductId(productId) {
        if (productId) {
            return productId;
        }
        return '';
    }

    private getProductType(productType) {
        if (productType) {
            return productType;
        }
        return '';
    }

    private getUserAgent() {
        return navigator.userAgent;
    }

    private getSource() {
        return window.location.href;
    }

    private getReference(reference ) {
        if (reference) {
            return reference;
        }
        return '';
    }

    private setTrackerMessage(event?) {
        let message = '';
        if (event) {
            message = event + ' on page ' + window.location.href;
        } else {
            message = 'Viewed Page ' + window.location.href;
        }
        return message;
    }

    private getTrackerId() {
        return sessionStorage.getItem('trackerid');
    }

    private buildRequestHeader() {
        const headers: Headers = new Headers({
            'Content-Type': 'application/json',
            'x-api-key': environment.trackerKey
        });
        return headers;
    }

    private postTracking(url, data) {
        const headers = this.buildRequestHeader();
        return this.http.post(url, data, { headers: headers })
            .map(response => {
                return response.json();
            })
            .catch(this.handleErrorObservable);
    }

    private handleErrorObservable(error: Response | any) {
        return Observable.throw(error);
    }

}



