import { Component } from '@angular/core';

import { ContactPageDetails } from './contact.model';
import { ContactDetailsService } from './contact-details-service';

@Component({
  selector: 'app-sales-details',
  templateUrl: './sales-details.component.html'
})
export class SalesDetailsComponent {

  contactPageDetails: ContactPageDetails = new ContactPageDetails;

  constructor(private contactDetailsService: ContactDetailsService) {
      this.fetchData();
  }

  fetchData() {
    if (localStorage.getItem('contact')) {
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
    }

    this.contactDetailsService.fetchData()
    .subscribe(data => {
      this.contactPageDetails = JSON.parse(localStorage.getItem('contact'));
    });
  }
}



