import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params, NavigationEnd, Router} from '@angular/router';

import { NgxGalleryOptions, NgxGalleryImage, NgxGalleryAnimation } from 'ngx-gallery';

import { GalleryService } from '@runway/asset';
import { HandlDetailComponent, HandlService, HandlSummary, HandlDetail } from '@runway/handl';
import { HomeService, RangeSummary } from '@runway/home';

import { TrackingService } from '../../../tracking-service/tracking.service';

@Component({
  selector: 'app-housedetail',
  templateUrl: './housedetails.component.html'
})
export class HouseDetailsComponent extends HandlDetailComponent {

  rangeList: RangeSummary[];
  productId: string;
  productType = 'H&L Package';
  inclusionList = [];

  constructor(
    protected handlService: HandlService,
    protected galleryService: GalleryService,
    protected activatedRoute: ActivatedRoute,
    private homeService: HomeService,
    private router: Router,
    private trackingService: TrackingService
  ) {
    super(handlService, galleryService, activatedRoute);

    this.activatedRoute.params.forEach((params) => {
      this.productId = params['id'];
      this.houseSummary();
    });
  }

  eventTracking(event?, referrer?) {
    let message: string;
    if (event) {
      message = event;
    } else {
      message = 'Viewed H&L package';
    }
    this.trackingService.eventTracking(message, referrer, this.productId, this.productType);
  }

  houseSummary() {
    this.handlService.getHandlSummary(this.productId).subscribe(data => {
      this.eventTracking(null, data.productname);
    });

    this.handlService.getHandlDetail(this.productId).subscribe(data => {
      const drips = data.handldrips.find(drip => (drip.dripname === 'GLOBAL DRIP' && drip.dripposition === 1)).dripdescription;
      this.inclusionList = drips.split('\n');
    });
  }

  listRangeSummary() {
    this.rangeList = [];
    return this.homeService.listRangeSummary()
      .subscribe((rangeList) => {
        this.rangeList = rangeList;

      });
  }

  getRangeLogoURL(rangeName: string): string {
    let rangeLogo: string;
    this.rangeList.forEach((range) => {
      if (range.productname === rangeName) {
        rangeLogo = range.imagepath;
        return rangeLogo;
      }
    });
    return rangeLogo;
  }

  scrollToView(id) {
    const element = document.getElementById(id);
    if (element) {
      element.scrollIntoView({behavior: 'smooth'});
    } else {
      this.router.navigate(['contact']);
    }
  }
}



