import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';

import { ContactComponent } from './contact.component';
import { EnquiryFormModule } from '../enquiry-form/enquiry-form.module';
import { EnquiryHeaderComponent } from './enquiry-header.component';
import { SalesDetailsComponent } from './sales-details.component';

@NgModule({
  imports: [
    CommonModule,
    EnquiryFormModule
  ],
  declarations: [ContactComponent, EnquiryHeaderComponent, SalesDetailsComponent],
  exports: [ContactComponent, EnquiryHeaderComponent, SalesDetailsComponent]
})
export class ContactModule { }



